import gsap from "gsap";
import { shema } from "../../shema";
import { getIsTouchDevice, linkIMG } from "../../helper/helper";
// !
function throttle(callback, delay) {
  let lastCall = 0;

  return function (...args) {
    const now = Date.now();

    if (now - lastCall >= delay) {
      lastCall = now;
      callback.apply(this, args);
    }
  };
}

setInterval(() => {
  const testRemove = document.querySelector('.testRemove')
  if (testRemove) testRemove.textContent = ''
}, 7000)

const throttledFn = (data) => {
  const testRemove = document.querySelector('.testRemove')
  if (testRemove) testRemove.textContent += data + ' '
  // if(testRemove) testRemove.textContent = JSON.stringify(data)
}
