const heroConfig = {
  steps: 9,
  w: 200,
  h: 300,
  cadrW: 100,
  cadrH: 75,
}
const T = {
  RUN: "RUN",
  SWIMMING: "SWIMMING",
  STAY: "STAY",
  JUMP: "JUMP",
}

const routeType = {
  Left: "ArrowLeft",
  Right: "ArrowRight"
}

let renderFrame = 0;
let appScreen = null;
let glassElement = null;
let see_groundOne = null;
let isBlockScrollTop = false;

const oneSizeHeight = window.innerHeight * 0.01;
const sizeHeight = window.innerHeight;
const deviceWidth = window.innerWidth



let sizeHero = {
  ground: '19vh',
  watter: '1vh'
}

let heightScrollTop = 0

if (deviceWidth <= 450) {
  sizeHero = {
    ground: oneSizeHeight * 17,
    watter: '1vh'
  }
  heightScrollTop = window.innerHeight * 0.85
} else {
  sizeHero = {
    ground: '19vh',
    watter: '2vh'
  }
  heightScrollTop = window.innerHeight * 0.7
}

// const sizeHero = {
//   ground : "19vh",
//   watter : '2vh' 
//   oneSizeHeight * 2
// }


const isTouchDevice = getIsTouchDevice()
let lastScrollLeft = 0;

let isStopScroll = false
let isHeroTransitioning = false;

export default function Hero({ run_game }) {
  const spriteRef = useRef(null);
  const [frame, setFrame] = useState(renderFrame);
  const [isTurn, setIsTurn] = useState(false)
  // const [bottomHero, setBottomHero] = useState(sizeHero.ground)
  const [positionHero, setPositionHero] = useState('auto')


  const changeOneFrame = (frame) => {
    renderFrame = frame;
    setFrame(frame)
  }

  const moveHero = (steps, typeRoute) => {
    const newFrameIndex = steps.indexOf(renderFrame);

    if (typeRoute === routeType.Right) {
      if (newFrameIndex === -1) return changeOneFrame(steps[0]);
      if (newFrameIndex === steps.length - 1) return changeOneFrame(steps[0]);
      return changeOneFrame(steps[newFrameIndex + 1]);

    } else if (typeRoute === routeType.Left) {
      const length = steps.length - 1;
      if (newFrameIndex === -1) return changeOneFrame(steps[length]);
      if (newFrameIndex === 0) return changeOneFrame(steps[length]);
      return changeOneFrame(steps[newFrameIndex - 1]);
    }
  }
  const changeTopHero = (is) => {
    const info = glassElement//.getBoundingClientRect();
    console.log(see_groundOne, window.innerHeight - 180)
    // if(is) setPositionHero( see_groundOne.top - window.innerHeight  + 'px')
    if (is) {

      if (deviceWidth <= 320) setPositionHero(window.innerHeight - 460 + 'px')
      else if (deviceWidth <= 590) setPositionHero(window.innerHeight - 280 + 'px')
      else if (deviceWidth <= 690) setPositionHero(window.innerHeight - 240 + 'px')
      else setPositionHero(window.innerHeight - 180 + 'px')
    } else {
      setPositionHero(info.top - 150 + 42 + 'px')
    }
  }

  // const changeTopPositionHero = (typeMove, preventDefault, turn) => {
  //   if (isBlockScrollTop) return preventDefault();
  //   const scrollTop = appScreen.scrollTop;
  //   const newTop = typeMove.top;

  //   // throttledFn({newTop, scrollTop, turn})

  //   if (scrollTop === 0 && newTop !== 0) {
  //     console.log(heightScrollTop, 'heightScrollTop')
  //     console.log(newTop)
  //     preventDefault()
  //     isBlockScrollTop = true;

  //     // const heightScrollTop = window.innerHeight * 0.7
  //     if (turn) {
  //       // блокировать скролл анимацию влево вправо до этого действия а потом включать псосле, нужно 2 переменных 1 новую создать, вторая isStopScroll
  //       appScreen.scrollTo({ top: heightScrollTop, left: typeMove.widthEnd - 90, behavior: "smooth" })
  //       if (sizeHeight >= 950) {
  //         setTimeout(() => isBlockScrollTop = false, 600)
  //       } else {
  //         setTimeout(() => isBlockScrollTop = false, 400)
  //       }

  //     } else {
  //       appScreen.scrollTo({ top: heightScrollTop, left: typeMove.widthStart + 90, behavior: "smooth" })
  //       if (sizeHeight >= 950) {
  //         setTimeout(() => isBlockScrollTop = false, 600)
  //       } else {
  //         setTimeout(() => isBlockScrollTop = false, 400)
  //       }
  //     }

  //   } else if (scrollTop !== 0 && newTop === 0) {
  //     console.log(heightScrollTop, 'heightScrollTop 2')
  //     console.log(newTop)
  //     preventDefault()
  //     isBlockScrollTop = true;

  //     if (turn) {
  //       appScreen.scrollTo({ top: newTop, left: typeMove.widthEnd - 90, behavior: "smooth" })
  //       if (sizeHeight >= 950) {
  //         setTimeout(() => isBlockScrollTop = false, 600)
  //       } else {
  //         setTimeout(() => isBlockScrollTop = false, 400)
  //       }
  //     } else {
  //       appScreen.scrollTo({ top: newTop, left: typeMove.widthStart + 90, behavior: "smooth" })
  //       if (sizeHeight >= 950) {
  //         setTimeout(() => isBlockScrollTop = false, 600)
  //       } else {
  //         setTimeout(() => isBlockScrollTop = false, 400)
  //       }
  //     }
  //   }
  // }


const changeTopPositionHero = (typeMove, preventDefault, turn) => {
  if (isBlockScrollTop || isHeroTransitioning) return preventDefault();

  const scrollTop = appScreen.scrollTop;
  const newTop = typeMove.top;

  const targetLeft = turn
    ? typeMove.widthEnd - 90
    : typeMove.widthStart + 90;

  const isGoingDown = scrollTop === 0 && newTop !== 0;
  const isGoingUp = scrollTop !== 0 && newTop === 0;

  if (isGoingDown || isGoingUp) {
    preventDefault();
    isBlockScrollTop = true;
    isHeroTransitioning = true;

    const scrollTarget = {
      top: isGoingDown ? heightScrollTop : newTop,
      behavior: "smooth",
    };

    if (Math.abs(appScreen.scrollLeft - targetLeft) > 10) {
      scrollTarget.left = targetLeft;
      lastScrollLeft = Math.max(targetLeft, 0);
    }

    appScreen.scrollTo(scrollTarget);

    setTimeout(() => {
      isBlockScrollTop = false;
      isHeroTransitioning = false;
    }, sizeHeight >= 820 ? 600 : 400);
  }
};


  const changeFrame = (typeRoute, preventDefault, turn) => {
    const scrollLeft = appScreen.scrollLeft;
    const typeMove = shema.heroMove.find(({ widthStart, widthEnd }) => scrollLeft >= widthStart - (turn ? 0 : 30) && widthEnd + (turn ? 30 : 0) >= scrollLeft);

    changeTopPositionHero(typeMove, preventDefault, turn)

    if (typeMove.type === T.SWIMMING) {
      changeTopHero(true)
    } else {
      // setBottomHero(sizeHero.ground); 
      changeTopHero()
    }


    switch (typeMove.type) {
      case T.RUN: return moveHero([1, 2], typeRoute);
      case T.SWIMMING: return moveHero([3, 4, 5], typeRoute);

      default: return setFrame((prev) => (prev + 1) % heroConfig.steps);
    }
  };

  useEffect(() => {
    gsap.to(spriteRef.current, {
      backgroundPositionX: `-${frame * heroConfig.w}px`,
      duration: 0.01,
      ease: `steps(${heroConfig.steps})`,
    });
  }, [frame]);

  useEffect(() => {
    appScreen = document.querySelector('#root')
    // glassElement = document.querySelector('.glassElem-0');
    glassElement = document.querySelector('.glassElem-0')?.getBoundingClientRect();
    see_groundOne = document.querySelector('#see_ground-0')?.getBoundingClientRect();
    if (run_game === false) return;


    const handleKeydown = (e) => {
      const preventDefault = () => { e.preventDefault(); }
      switch (e.key) {
        case routeType.Left: setIsTurn(true); changeFrame(e.key, preventDefault, true); break;
        case routeType.Right: setIsTurn(false); changeFrame(e.key, preventDefault, false); break;
      }
    };



    // const handleScroll = (e) => {
    //   // if(isStopScroll ) return;
    //   if (isStopScroll || isBlockScrollTop) return e.preventDefault()
    //   isStopScroll = true;
    //   setTimeout(() => isStopScroll = false, 220)
    //   // setTimeout(() => isStopScroll = false, 120)
    //   let currentScrollLeft = appScreen.scrollLeft || window.pageXOffset;


    //   // const s = currentScrollLeft > lastScrollLeft ? 'Right' : 'Left';
    //   // throttledFn('||' + s + ' ' + 'scroll = ' + currentScrollLeft + ' ls = ' + lastScrollLeft + '||' )

    //   if (currentScrollLeft >= lastScrollLeft) { // >= may be
    //     setIsTurn(false); changeFrame(routeType.Right, () => { }, false);
    //     throttledFn('Right||')
    //   } else if (currentScrollLeft < lastScrollLeft) {
    //     throttledFn('Left||')
    //     setIsTurn(true); changeFrame(routeType.Left, () => { }, true);
    //   }

    //   // lastScrollLeft = currentScrollLeft <= 0 ? 0 : currentScrollLeft;

    //   const diff = currentScrollLeft - lastScrollLeft;
    //   if (Math.abs(diff) < 2) return; // игнорировать микродвижения

    //   if (diff > 0) {
    //     // Scroll Right
    //   } else {
    //     // Scroll Left
    //   }

    // };

    const handleScroll = (e) => {
      if (isStopScroll || isBlockScrollTop) {
        e.preventDefault();
        return;
      }

      isStopScroll = true;
      setTimeout(() => isStopScroll = false, 220);

      const currentScrollLeft = appScreen?.scrollLeft ?? document.documentElement.scrollLeft ?? window.pageXOffset;
      const diff = currentScrollLeft - lastScrollLeft;

      if (Math.abs(diff) < 3) return; // игнорируем микродвижения

      if (diff > 0) {
        setIsTurn(false);
        changeFrame(routeType.Right, () => { }, false);
        throttledFn('Right');
      } else {
        setIsTurn(true);
        changeFrame(routeType.Left, () => { }, true);
        throttledFn('Left');
      }

      lastScrollLeft = Math.max(currentScrollLeft, 0);
    };

    const throttledScroll = throttle(handleScroll, 100);
    window.addEventListener("keydown", handleKeydown);
    // isTouchDevice && appScreen.addEventListener("scroll", handleScroll);
    isTouchDevice && appScreen.addEventListener("scroll", throttledScroll, { passive: false });
    appScreen?.focus();

    if (glassElement) {
      changeTopHero()
      // высота расположении травы - высота персонажа + высота травы
    }

    return () => {
      window.removeEventListener("keydown", handleKeydown);
      // isTouchDevice && appScreen.removeEventListener("scroll", handleScroll);
      isTouchDevice && appScreen.removeEventListener("scroll", throttledScroll);
    };
  }, [run_game]);


  return (
    <div className="flex flex-col items-center mt-10" style={{ position: 'fixed', top: positionHero, bottom: '19vh', left: '6vw', height: '135px', width: '200px', zIndex: 20 }}>
      {/* <div className="flex flex-col items-center mt-10" style={{ position: 'fixed', bottom: bottomHero, left: '6vw', height: '135px', width: '200px', zIndex: 20 }}> */}
      <div
        ref={spriteRef}
        className={`w-[${heroConfig.w}px] h-[${heroConfig.h}px]`}
        style={{
          backgroundImage: `url('${linkIMG}/hero/robby-slides.png')`,
          backgroundSize: `${heroConfig.steps * heroConfig.w}px ${heroConfig.h}px`,
          width: `${heroConfig.w}px`,
          height: `${heroConfig.h / 2}px`,
          transform: `rotateY(${isTurn ? 180 : 0}deg)`,
          opacity: run_game ? 1 : 0
        }}
      />

    </div>
  );
}
